<template>
  <div v-if="ordersData.list && ordersData.list.order_list">
    <el-dialog :visible="visible" title="详情" center :before-close="close">
      <div class="detail">
        <div class="item">
          <span class="label">ID:</span>
          <span>{{ currentItem.id }}</span>
        </div>
        <div class="item">
          <span class="label">订单编号:</span>
          <span>{{ currentItem.order_sn }}</span>
        </div>
        <div class="item">
          <span class="label">商家名称:</span>
          <span>{{ currentItem.shop_name }}</span>
        </div>
        <div class="item">
          <span class="label">用户ID:</span>
          <span>{{ currentItem.user_id }}</span>
        </div>
        <div class="item">
          <span class="label">商家ID:</span>
          <span>{{ currentItem.shop_id }}</span>
        </div>
        <div class="item">
          <span class="label">件数:</span>
          <span>{{ currentItem.goods_num }}</span>
        </div>
        <div class="item">
          <span class="label">商品名称:</span>
          <span>{{ currentItem.goods_name }}</span>
        </div>
        <div class="item">
          <span class="label">物流公司:</span>
          <span>{{ currentItem.express_company }}</span>
        </div>
        <div class="item">
          <span class="label">物流单号:</span>
          <span>{{ currentItem.express_no }}</span>
        </div>
        <div class="item">
          <span class="label">付款时间:</span>
          <span>{{ currentItem.pay_way }}</span>
        </div>
        <div class="item">
          <span>发货时间:</span>
          <span>{{ currentItem.delivery_time }}</span>
        </div>
        <div class="item">
          <span class="label">签收时间:</span>
          <span>{{ currentItem.submission_time }}</span>
        </div>
        <div class="item">
          <span class="label">付款方式:</span>
          <span>{{ currentItem.pay_way | payWay }}</span>
        </div>
        <div class="item">
          <span class="label">订单状态:</span>
          <span>{{ currentItem.status | orderStatus }}</span>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="visible = false">确 定</el-button>
      </span>
    </el-dialog>
    <header class="searchContainer">
      <el-input class="commen" placeholder="请输入订单编号" />
      <el-input class="commen" placeholder="请输入用户ID" />
      <el-input class="commen" placeholder="请输入昵称" />
      <el-input class="commen" placeholder="请输入手机号码" />
      <el-input class="commen" placeholder="请输入商品名称" />
      <el-input class="commen" placeholder="请输入物流单号" />
      <el-date-picker
        type="date"
        v-model="time"
        placeholder="发货时间"
        :picker-options="pickerOptions"
      />
      <el-select
        style="width: 120px"
        v-model="value"
        placeholder="商品状态"
        clearable
      >
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        />
      </el-select>
      <el-button class="confirm" type="primary">
        确定
      </el-button>
      <!-- <div class="export">
        <JsonExcel
          :data="ordersData.list.order_list"
          :fields="fields"
          name="订单.xls"
        >
          <el-button type="primary">
            导出订单
          </el-button>
        </JsonExcel>
      </div> -->
    </header>
    <div class="ordersList">
      <el-table
        :data="ordersData.list.order_list"
        border
        style="width=100%"
        :header-cell-style="{ 'text-align': 'center', color: 'black' }"
        :cell-style="{ 'text-align': 'center', padding: '5px 0' }"
      >
        <el-table-column prop="id" label="序号" />
        <el-table-column
          prop="order_sn"
          label="订单编号"
          :show-overflow-tooltip="true"
        />
        <el-table-column
          label="商家名称"
          prop="shop_name"
          :show-overflow-tooltip="true"
        />

        <el-table-column prop="user_id" label="用户ID" />
        <el-table-column prop="shop_id" label="商家ID" />
        <el-table-column label="件数" prop="goods_num" />
        <el-table-column
          prop="goods_name"
          label="商品名称"
          :show-overflow-tooltip="true"
        />
        <el-table-column prop="express_company" label="物流公司" />
        <el-table-column prop="express_no" label="物流单号" />
        <el-table-column label="付款时间" prop="pay_time" />
        <el-table-column label="发货时间" prop="delivery_time" />
        <el-table-column label="签收时间" prop="submission_time" />
        <el-table-column label="付款方式">
          <template slot-scope="scope">
            <div>{{ scope.row.pay_way | payWay }}</div>
          </template>
        </el-table-column>
        <el-table-column label="订单状态">
          <template slot-scope="scope">
            <div>{{ scope.row.status | orderStatus }}</div>
          </template>
        </el-table-column>
        <el-table-column>
          <template slot="header" :slot-scope="scope">
            操作
          </template>
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="look(scope.row.id)">
              查看
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        layout="prev,pager,next"
        background
        :page-size="limit"
        :total="ordersData.total"
        @current-change="changePage"
        @prev-click="prev"
        @next-click="next"
      />
    </div>
  </div>
</template>

<script>
// import JsonExcel from "vue-json-excel";

export default {
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
      },
      currentItem: {},
      visible: false,
      // 发货时间
      time: "",
      // 选中的商品状态
      options: [
        {
          value: "选项1",
          label: "黄金糕",
        },
        {
          value: "选项2",
          label: "双皮奶",
        },
        {
          value: "选项3",
          label: "蚵仔煎",
        },
        {
          value: "选项4",
          label: "龙须面",
        },
        {
          value: "选项5",
          label: "北京烤鸭",
        },
      ],
      // 商品状态
      value: "",
      pages: 1,
      limit: 15,
      scope: "scope",
      ordersData: [],
      fields: {
        "序号": "id",
        "订单编号": "order_sn",
        "商家名称": "shop_name",
        "用户ID": "user_id",
        "商家ID": "shop_id",
        "件数": "goods_num",
        "商品名称": "goods_name",
        "物流公司": "express_company",
        "物流单号": "express_no",
        "付款时间": "pay_time",
        "发货时间": "delivery_time",
        "签收时间": "submission_time",
        "付款方式": {
          field: "pay_way",
          callback: (value) => {
            let word = "";
            const payWay = [
              {
                code: 1,
                way: "微信",
              },
              {
                code: 2,
                way: "支付宝",
              },
              {
                code: 3,
                way: "余额 ",
              },
              {
                code: 4,
                way: "消费券 ",
              },
              {
                code: 5,
                way: "积分",
              },
              {
                code: 6,
                way: "银行卡",
              },
            ];

            payWay.forEach((item) => {
              if (item.code === value) {
                word = item.way;
              }
             
            });
             return word;
          },
        },
        "订单状态": {
          field: "status",
          callback: (value) => {
            let word = "";

            const status = [
              {
                code: 1,
                status: "待支付",
              },
              {
                code: 2,
                status: "待选择",
              },
              {
                code: 3,
                status: "待发货",
              },
              {
                code: 4,
                status: "待收获",
              },
              {
                code: 5,
                status: "已完成",
              },
              {
                code: 6,
                status: "已取消",
              },
              {
                code: 7,
                status: "销售中",
              },
            ];

            status.forEach((item) => {
              if (value === item.code) {
                word =  item.status;
              }
            });
            return word;
          },
        },
      },
    };
  },
  created() {
    this.getOrders();
  },
  filters: {
    payWay(payCode) {
      let word = "";
      const payWay = [
        {
          code: 1,
          way: "微信",
        },
        {
          code: 2,
          way: "支付宝",
        },
        {
          code: 3,
          way: "余额 ",
        },
        {
          code: 4,
          way: "消费券 ",
        },
        {
          code: 5,
          way: "积分",
        },
        {
          code: 6,
          way: "银行卡",
        },
      ];

      payWay.forEach((item) => {
        if (item.code === payCode) {
           word = item.way;
           return;
        }
      });
      return word;
    },

    orderStatus(statusCode) {
      let word = "";
      const status = [
        {
          code: 1,
          status: "待支付",
        },
        {
          code: 2,
          status: "待选择",
        },
        {
          code: 3,
          status: "待发货",
        },
        {
          code: 4,
          status: "待收获",
        },
        {
          code: 5,
          status: "已完成",
        },
        {
          code: 6,
          status: "已取消",
        },
        {
          code: 7,
          status: "销售中",
        },
      ];

      status.forEach((item) => {
        if (statusCode === item.code) {
          word =  item.status;
          return;
        }
      });
      return word;
    },
  },
  methods: {
    async getOrders() {
      try {
        let res = await this.axios.get("order/list", {
          params: {
            limit: this.limit,
            pages: this.pages,
            // 普通订单
            type: 1,
          },
        });
        this.ordersData = res.data;
      } catch (error) {
        console.log(error);
      }
    },
    // 查看
    look(id) {
      const currentItem = this.ordersData.list.order_list.find(
        (item) => item.id === id
      );
      this.currentItem = currentItem;
      this.visible = true;
    },
    // 翻页
    changePage(page) {
      this.pages = page;
      this.getOrders();
    },
    // 上一页
    prev() {
      this.pages--;
      this.getOrders();
    },
    // 下一页
    next() {
      this.pages++;
      this.getOrders();
    },
    close() {
      this.visible = false;
    },
  },
  components: {
    // JsonExcel,
  },
};
</script>

<style lang="scss" scoped>
.detail {
  .item {
    padding: 10px 0;
    .label {
      display: inline-block;
      width: 120px;
    }
  }
}
.searchContainer {
  .commen {
    width: 170px;
    margin-top: 10px;
  }
  .confirm,
  .export {
    margin-top: 10px;
    display: inline-block;
  }
  .export {
    margin-left: 30px;
  }
}
.ordersList {
  height: 800px;
  margin-top: 10px;
}
</style>
